@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap');

*{
    padding: 0; 
    margin: 0;
    box-sizing: border-box;
    font-family: 'Titillium Web', sans-serif;
    overflow: hidden;
    scroll-behavior: smooth;
}

:root {
    --mainColor: #20232a;
    --mainColorLight: #5767aa;
    --SecondaryColor: #DB2B39;
    --textColor: #fff;
}

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;
    padding: 0 2rem;
    color: var(--textColor);
    position: absolute;
    width: 100%;
}

.navBar{
    background-color: transparent;
}
  
.navBar.colorChange{
    background-color: #000;
}

nav a{
    margin: 0 4.5rem;
    color: var(--textColor);
    text-decoration: none;
    text-align-last: center;
    margin-top: 70px;
}
nav a:hover{
    color: var(--SecondaryColor);
}
h3 a{
    text-decoration: none;
    color: var(--SecondaryColor);
    cursor: pointer;
    font-size: 30px;
}

h3 a:hover{
    color: var(--textColor);
}

header .nav-btn{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

@media only screen and (max-width: 1024px){

    header .nav-btn {
        visibility: visible;
        opacity: 1;
    }

    header nav{
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-content: center;
        gap: 1.5rem;
        background-color: var(--mainColor);
        transition: 1s;
        transform: translateY(-100vh);
    }

    header .responsive_nav{
        transform: none;
    }

    nav .nav-close-btn{
        position: absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a{
            font-size: 1.2rem;
}
}